import	dragball from '../../../components/drag-ball/drag-ball.vue'
export default {
	data() {
		return {
			comment_pageNum: 1,
			comment_pageSize: 5,
			comment_totalNum: 0,
			commentList: [],
			note_pageNum: 1,
			note_pageSize: 3,
			note_totalNum: 0,
			noteList: [],
			noteIsLoved: [],

			isCollection: false,

			loved: false,
			scenic_strategy_love: false,
			latitude: 0,  //纬度
			longitude: 0,  //经度
			scale: 5, //最小数，缩放范围最大，可见程度最大
			scale: 18, //最大数，缩放范围最小，可见程度最小
			scenicId: 0,
			scenicDetail: {},
			markers: [{
				id: 0,
				latitude: 0,
				longitude: 0,
				iconPath: '/static/img/location.png',
				width: 40,
				height: 40
			}]
		}
	},
	components: {
	  dragball
	},
	onShow: function() {
		this.getCurrentLocation()
		this.getCommentList()
		this.getTravelNotesList()
		this.isCollectionLoad()
	},
	onLoad: function(option) {
		this.scenicId = option.scenic_id
		this.getDetailById(option.scenic_id)
	},
	methods: {
		dianzanScenicStrategy() {
			var _this = this
			if (this.scenic_strategy_love) {
			 uni.showToast({
			 	title: '亲，你赞过了!',
			 	duration: 2000
			 });
			  return
			}
			uni.request({
				url: this.$BASE_HOST + 'scenic/scenicStrategyLoved',
				method: 'GET',
				data: {
					'scenic_id': _this.scenicId
				},
				success: function (res) {
					if (res.data.code === 20000) {
							uni.showToast({
								title: '点赞成功',
								duration: 2000
							});
					    _this.scenic_strategy_love = true
					    _this.scenicDetail.scenicStrategyLove = _this.scenicDetail.scenicStrategyLove + 1
					} else {
					 uni.showToast({
					 	title: '服务异常',
					 	duration: 2000
					 });
					}
				}
			})
		},
		toShop() {
			console.log('to shop', this.scenicId)
			uni.navigateTo({
				url:'../shopping/shopping?scenic_id=' + this.scenicId
			})
		},
		isCollectionLoad() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'collection/isCollection',
				method: 'POST',
				data: {
					'userId': uni.getStorageSync('local_userinfo').id,
					'scenicId': _this.scenicId
				},
				success: function (res) {
					if (res.data.code == 20003) {
						_this.isCollection = false
					}else if(res.data.code == 20002){
						_this.isCollection = true
					}
				}
			})
		},
		collection() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'collection/add',
				method: 'POST',
				data: {
					'userId': uni.getStorageSync('local_userinfo').id,
					'scenicId': _this.scenicId
				},
				success: function (res) {
					if (res.data.code === 20000){
						_this.isCollection = true
					} else if(20002) {
						_this.isCollection = false
					}
				}
			})
		},
		youjiDianzan(item, index) {
			var _this = this

			if (this.noteIsLoved[index]){
				uni.showToast({
					title: '亲，你赞过了!',
					duration: 2000
				});
				return
			}

			// 游记点赞
			uni.request({
				url: this.$BASE_HOST + 'travelnotes/dianzan',
				method: 'get',
				data: {
					id: item.id
				},
				success: function () {
					_this.noteList[index].note_love = _this.noteList[index].note_love + 1
					_this.noteIsLoved[index] = true
				}
			})

		},
		getTravelNotesList(){
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'travelnotes/getPageOfOrderByDianzan',
				method: 'get',
				data: {
					'pageNum': _this.note_pageNum,
					'pageSize': _this.note_pageSize,
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': _this.scenicId,
				},
				success: function (res) {
					_this.noteList = res.data.data.rows
					_this.note_totalNum = res.data.data.total

					_this.noteList.forEach((item,index)=> {
						item.note_img = item.note_img.split(',')
						for(var x=0;x<item.note_img.length;x++){
							item.note_img[x] = _this.$BASE_HOST + item.note_img[x]
						}
						item.note_video =  _this.$BASE_HOST + item.note_video
						item.user_avatar = 'background-image:url(' + item.user_avatar +')'
						item.create_date = item.create_date.substring(0,10)
					})
				}
			})
		},
		getMoreComment() {
			this.comment_pageNum = this.comment_pageNum + 1
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'comment/getPageComment',
				method: 'get',
				data: {
					'pageNum': _this.comment_pageNum,
					'pageSize': _this.comment_pageSize,
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': _this.scenicId,
				},
				success: function (res) {
					_this.comment_totalNum = res.data.data.total
					res.data.data.rows.forEach((item,index) => {
						item.comment_img = item.comment_img.split(',')
						for(var x=0;x<item.comment_img.length;x++){
							item.comment_img[x] = _this.$BASE_HOST + item.comment_img[x]
						}
						item.user_avatar = 'background-image:url(' + item.user_avatar +')'
						item.create_date = item.create_date.substring(0,10)
						_this.commentList.push(item)
					})

					/*_this.commentList.forEach((item,index)=> {
						item.comment_img = item.comment_img.split(',')
						for(var x=0;x<item.comment_img.length;x++){
							item.comment_img[x] = _this.$BASE_HOST + item.comment_img[x]
						}
						item.user_avatar = 'background-image:url(' + item.user_avatar +')'
						item.create_date = item.create_date.substring(0,10)

					})*/
				}
			})
		},
		getCommentList() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'comment/getPageComment',
				method: 'get',
				data: {
					'pageNum': _this.comment_pageNum,
					'pageSize': _this.comment_pageSize,
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': _this.scenicId,
				},
				success: function (res) {
					_this.commentList = res.data.data.rows
					_this.comment_totalNum = res.data.data.total

					_this.commentList.forEach((item,index)=> {
						item.comment_img = item.comment_img.split(',')
						for(var x=0;x<item.comment_img.length;x++){
							item.comment_img[x] = _this.$BASE_HOST + item.comment_img[x]
						}
						if (item.user_avatar.substring(0,5) !== 'https'){
						   item.user_avatar = _this.$BASE_HOST + item.user_avatar
						}
						item.user_avatar = 'background-image:url(' + item.user_avatar +')'
						item.create_date = item.create_date.substring(0,10)

					})
				}
			})
		},
		addYouji() {
			this.isLogin()
			uni.navigateTo({
				url:'../youji_add/youji_add?scenicId=' + this.scenicId
			})
		},
		addComment() {
			this.isLogin()
			var _this = this
			uni.navigateTo({
				url: '../comment_add/comment_add?scenicId='+_this.scenicId
			})
		},
		isLogin(){
			if (!uni.getStorageSync('local_userinfo')) {
				uni.showToast({
					title: '请登录哦!',
					duration: 2000
				});
				uni.navigateTo({
					url: '../../base/login/login'
				})
			}
		},
		shoucang() {
			this.isLogin()
		},
		dianzan() {
			this.isLogin()
			var _this = this
			if (this.loved) {
				uni.showToast({
					title: '亲，已赞过了',
					duration: 2000
				});
				return
			}
			uni.request({
				url: this.$BASE_HOST + 'scenic/dianzan',
				method: 'get',
				data: {
					'id': _this.scenicId
				},
				success: function (res) {
					_this.loved = true
					_this.scenicDetail.scenicLove = _this.scenicDetail.scenicLove + 1
				}
			})
		},
		getCurrentLocation() {
			// var _this = this
			// uni.getLocation({
			// 	type: 'wgs84',
			// 	success: function (res) {
			// 		console.log(res)
			// 		_this.latitude = res.latitude
			// 		_this.longitude = res.longitude
			// 		_this.markers[0].latitude = res.latitude
			// 		_this.markers[0].longitude = res.longitude
			// 	}
			// });
		},
		getDetailById(id){
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'scenic/getById',
				method: 'get',
				data: {
					'id': id
				},
				success: function (res) {
					_this.scenicDetail = res.data.data
					_this.scenicDetail.scenicType = _this.scenicDetail.scenicType.split(',')
					_this.longitude = _this.scenicDetail.scenicLng
					_this.latitude = _this.scenicDetail.scenicLat
					_this.markers[0].latitude = _this.latitude
					_this.markers[0].longitude = _this.longitude
				}
			})
		}
	}
}
